<template>
	<div>
		<div class="welcome">
			管理员：<span style="color: #2a9f93;">{{username}}</span>
			当前时间：<span style="color: #F56C6C;">{{ nowDate + ' ' + nowTime + ' ' + nowWeek }}</span>
		</div>
		<div class="box">
			<div class="box-item">
				<div class="btn">
					<router-link to="/infomation">更多学生信息</router-link>
				</div>
				<div class="btn total">总数：{{studentTotal}}</div>
				<el-table :data="studentData">
					<el-table-column prop="name" label="姓名" align="center"></el-table-column>
					<el-table-column prop="number" label="学号" align="center"></el-table-column>
					<el-table-column prop="sex" label="性别" align="center"></el-table-column>
					<el-table-column prop="age" label="年龄" align="center"></el-table-column>
				</el-table>
			</div>
			<div class="box-item">
				<div class="btn">
					<router-link to="/grade">更多班级信息</router-link>
				</div>
				<div class="btn total">总数：{{classTotal}}</div>
				<el-table :data="classData">
					<el-table-column prop="id" label="ID" align="center"></el-table-column>
					<el-table-column prop="grade" label="年级" align="center"></el-table-column>
					<el-table-column prop="title" label="班级" align="center"></el-table-column>
					<el-table-column prop="teacher.name" label="班主任" align="center"></el-table-column>
				</el-table>
			</div>
			<div class="box-item">
				<div class="btn">
					<router-link to="/family">更多家长信息</router-link>
				</div>
				<div class="btn total">总数：{{familyTotal}}</div>
				<el-table :data="familyData">
					<el-table-column prop="student.name" label="学生姓名" align="center"></el-table-column>
					<el-table-column prop="UserInfo.name" label="家长姓名" align="center"></el-table-column>
					<el-table-column prop="UserInfo.phone" label="手机号" align="center"></el-table-column>
					<el-table-column prop="WxUser.nickname" label="微信名" align="center"></el-table-column>
				</el-table>
			</div>
			<div class="box-item">
				<div class="btn">
					<router-link to="/teacher">更多教职工信息</router-link>
				</div>
				<div class="btn total">总数：{{teacherTotal}}</div>
				<el-table :data="teacherData">
					<el-table-column prop="name" label="姓名" align="center"></el-table-column>
					<el-table-column prop="phone" label="手机号" align="center"></el-table-column>
					<el-table-column prop="state" label="是否为班主任" align="center">
						<template slot-scope="scope">
							<el-tag disable-transitions>{{scope.row.worker}}</el-tag>
						</template>
					</el-table-column>
					<el-table-column prop="sex" label="状态" align="center">
						<template slot-scope="scope">
							<el-tag disable-transitions>{{scope.row.state}}</el-tag>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</div>
	</div>
</template>

<script>
	import API from '@/api/index.js'
	export default {
		data() {
			return {
				username: localStorage.getItem('username'),
				// currentTime: '',
				studentData: [],
				classData: [],
				familyData: [],
				teacherData: [],
				studentTotal: '',
				classTotal: '',
				familyTotal: '',
				teacherTotal: '',
				nowDate: "", // 当前日期
				nowTime: "", // 当前时间
				nowWeek: "", // 当前星期

				permissions: localStorage.getItem('permissions'),
				role: localStorage.getItem('role')
			}
		},
		mounted() {
			this.getInfo()
			this.currentTime();
		},
		methods: {
			currentTime() {
				setInterval(this.getDate, 500);
			},
			getDate() {
				var self = this;
				let yy = new Date().getFullYear();
				let mm = new Date().getMonth() + 1;
				let dd = new Date().getDate();
				let week = new Date().getDay();
				let hh = new Date().getHours();
				let mf =
					new Date().getMinutes() < 10 ?
					"0" + new Date().getMinutes() :
					new Date().getMinutes();
				let ss = new Date().getSeconds();
				switch (week) {
					case 1:
						self.nowWeek = "星期一";
						break;
					case 2:
						self.nowWeek = "星期二";
						break;
					case 3:
						self.nowWeek = "星期三";
						break;
					case 4:
						self.nowWeek = "星期四";
						break;
					case 5:
						self.nowWeek = "星期五";
						break;
					case 6:
						self.nowWeek = "星期六";
						break;
					case 7:
						self.nowWeek = "星期日";
				}
				self.nowTime = hh + ":" + mf + ":" + ss;
				self.nowDate = yy + "-" + mm + "-" + dd;
			},
			getInfo() {
				var self = this;
				API.students(1, 5).then(res => {
					self.studentData = res.data;
					self.studentTotal = res.total;
				})
				API.classes(1, 5).then(res => {
					self.classData = res.data;
					self.classTotal = res.total;
				})
				API.family(1, 5).then(res => {
					self.familyData = res.data;
					self.familyTotal = res.total;
				})
				API.teacher(1, 5).then(res => {
					self.teacherData = res.data;
					self.teacherTotal = res.total;
					self.teacherData.forEach(item => {
						switch (item.state) {
							case 1:
								item.state = '待审核';
								break;
							case 2:
								item.state = '已通过';
								break;
							case 3:
								item.state = '未通过';
						}
						switch (item.worker) {
							case 0:
								item.worker = '否'
								break;
							case 1:
								item.worker = '否'
								break;
							case 2:
								item.worker = '是'
								break;
							case 3:
								item.worker = '否'
						}
					})
				})
			}
		},
		// 销毁定时器
		beforeDestroy: function() {
			if (this.getDate) {
				clearInterval(this.getDate);
			}
		}
	}
</script>

<style scoped lang="scss">
	.welcome {
		padding: 20px 10px;
		box-shadow: 1px 1px 5px #ccc;
		font-size: 16px;
	}

	.welcome span {
		padding: 20px 5px;
	}

	.box {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.box-item {
			width: 48%;
			margin-top: 30px;
			// margin-left: 50px;
			padding: 10px;
			box-shadow: 1px 1px 5px #ccc;

			.btn {
				padding: 10px;
				border-radius: 5px;
				background: #2a9f93;

				a {
					color: #fff;
				}
			}

			.total {
				background: #f0f0f0;
				color: #2a9f93;
			}
		}
	}
</style>
